import { Player, Enemy, Bullet, Explosion } from './entities'
import { ENEMY_SPAWN_INTERVAL, GAME_WIDTH, GAME_HEIGHT, SCORE_PER_KILL } from './constants'

export function initGame(
  canvas: HTMLCanvasElement,
  onGameOver: (score: number) => void,
  onScoreUpdate: (score: number) => void
) {
  const ctx = canvas.getContext('2d')
  if (!ctx) {
    console.error('Could not get 2D context from canvas')
    return () => {}
  }

  let score = 0
  let isGameOver = false
  let lastEnemySpawn = 0

  const player = new Player()
  const enemies: Enemy[] = []
  const bullets: Bullet[] = []
  const explosions: Explosion[] = []

  const keys = {
    ArrowLeft: false,
    ArrowRight: false,
    Space: false,
    lastShot: 0
  }

  function handleKeyDown(e: KeyboardEvent) {
    if (e.code === 'ArrowLeft') keys.ArrowLeft = true
    if (e.code === 'ArrowRight') keys.ArrowRight = true
    if (e.code === 'Space') keys.Space = true
  }

  function handleKeyUp(e: KeyboardEvent) {
    if (e.code === 'ArrowLeft') keys.ArrowLeft = false
    if (e.code === 'ArrowRight') keys.ArrowRight = false
    if (e.code === 'Space') keys.Space = false
  }

  window.addEventListener('keydown', handleKeyDown)
  window.addEventListener('keyup', handleKeyUp)

  function spawnEnemy(timestamp: number) {
    if (timestamp - lastEnemySpawn > ENEMY_SPAWN_INTERVAL) {
      enemies.push(new Enemy())
      lastEnemySpawn = timestamp
    }
  }

  function updatePlayer() {
    if (keys.ArrowLeft) player.moveLeft()
    if (keys.ArrowRight) player.moveRight()
    if (keys.Space && Date.now() - keys.lastShot > 250) {
      bullets.push(player.shoot())
      keys.lastShot = Date.now()
    }
  }

  function updateEntities() {
    // Update bullets
    for (let i = bullets.length - 1; i >= 0; i--) {
      if (bullets[i].update()) {
        bullets.splice(i, 1)
      }
    }

    // Update enemies
    for (let i = enemies.length - 1; i >= 0; i--) {
      if (enemies[i].update()) {
        enemies.splice(i, 1)
        isGameOver = true
      }
    }

    // Update explosions
    for (let i = explosions.length - 1; i >= 0; i--) {
      if (explosions[i].update()) {
        explosions.splice(i, 1)
      }
    }
  }

  function checkCollisions() {
    // Check bullet-enemy collisions
    for (let i = bullets.length - 1; i >= 0; i--) {
      for (let j = enemies.length - 1; j >= 0; j--) {
        if (bullets[i] && enemies[j] && bullets[i].collidesWith(enemies[j])) {
          explosions.push(new Explosion(enemies[j].x, enemies[j].y))
          bullets.splice(i, 1)
          enemies.splice(j, 1)
          score += SCORE_PER_KILL
          onScoreUpdate(score)
          break
        }
      }
    }

    // Check player-enemy collisions
    for (let i = enemies.length - 1; i >= 0; i--) {
      if (player.collidesWith(enemies[i])) {
        isGameOver = true
        break
      }
    }
  }

  function draw() {
    if (!ctx) return

    // Clear canvas
    ctx.fillStyle = '#000000'
    ctx.fillRect(0, 0, GAME_WIDTH, GAME_HEIGHT)

    // Draw game entities
    player.draw(ctx)
    bullets.forEach(bullet => bullet.draw(ctx))
    enemies.forEach(enemy => enemy.draw(ctx))
    explosions.forEach(explosion => explosion.draw(ctx))
  }

  function gameLoop(timestamp: number) {
    if (isGameOver) {
      onGameOver(score)
      return
    }

    spawnEnemy(timestamp)
    updatePlayer()
    updateEntities()
    checkCollisions()
    draw()

    requestAnimationFrame(gameLoop)
  }

  requestAnimationFrame(gameLoop)

  // Cleanup function
  return () => {
    window.removeEventListener('keydown', handleKeyDown)
    window.removeEventListener('keyup', handleKeyUp)
    isGameOver = true
  }
}
