'use client'

import { useEffect, useState } from 'react'
import { Card } from '@/components/ui/card'
import GameCanvas from '@/components/game/Canvas'
import GameOver from '@/components/game/GameOver'
import Score from '@/components/game/Score'

export default function GamePage() {
  const [score, setScore] = useState(0)
  const [isGameOver, setIsGameOver] = useState(false)

  const handleGameOver = (finalScore: number) => {
    setScore(finalScore)
    setIsGameOver(true)
  }

  const handleRestart = () => {
    setScore(0)
    setIsGameOver(false)
  }

  return (
    <div className="min-h-screen bg-black p-4 flex flex-col items-center justify-center">
      <h1 className="text-4xl font-bold text-white mb-4">Space Shooter</h1>
      <Card className="p-4 bg-gray-900 border-gray-800">
        {!isGameOver ? (
          <>
            <Score score={score} />
            <GameCanvas onGameOver={handleGameOver} onScoreUpdate={setScore} />
          </>
        ) : (
          <GameOver score={score} onRestart={handleRestart} />
        )}
      </Card>
      <div className="mt-4 text-gray-400 text-sm">
        Use arrow keys to move and spacebar to shoot
      </div>
    </div>
  )
}
