import {
  PLAYER_WIDTH,
  PLAYER_HEIGHT,
  PLAYER_SPEED,
  BULLET_WIDTH,
  BULLET_HEIGHT,
  BULLET_SPEED,
  ENEMY_WIDTH,
  ENEMY_HEIGHT,
  ENEMY_SPEED,
  COLORS,
  GAME_WIDTH,
  GAME_HEIGHT
} from './constants'

export class Entity {
  constructor(
    public x: number,
    public y: number,
    public width: number,
    public height: number,
    public color: string
  ) {}

  draw(ctx: CanvasRenderingContext2D) {
    ctx.fillStyle = this.color
    ctx.fillRect(this.x, this.y, this.width, this.height)
  }

  collidesWith(other: Entity): boolean {
    return (
      this.x < other.x + other.width &&
      this.x + this.width > other.x &&
      this.y < other.y + other.height &&
      this.y + this.height > other.y
    )
  }
}

export class Player extends Entity {
  constructor() {
    super(
      GAME_WIDTH / 2 - PLAYER_WIDTH / 2,
      GAME_HEIGHT - PLAYER_HEIGHT - 10,
      PLAYER_WIDTH,
      PLAYER_HEIGHT,
      COLORS.PLAYER
    )
  }

  moveLeft() {
    this.x = Math.max(0, this.x - PLAYER_SPEED)
  }

  moveRight() {
    this.x = Math.min(GAME_WIDTH - this.width, this.x + PLAYER_SPEED)
  }

  shoot(): Bullet {
    return new Bullet(
      this.x + this.width / 2 - BULLET_WIDTH / 2,
      this.y
    )
  }
}

export class Enemy extends Entity {
  constructor() {
    super(
      Math.random() * (GAME_WIDTH - ENEMY_WIDTH),
      -ENEMY_HEIGHT,
      ENEMY_WIDTH,
      ENEMY_HEIGHT,
      COLORS.ENEMY
    )
  }

  update() {
    this.y += ENEMY_SPEED
    return this.y > GAME_HEIGHT
  }
}

export class Bullet extends Entity {
  constructor(x: number, y: number) {
    super(
      x,
      y,
      BULLET_WIDTH,
      BULLET_HEIGHT,
      COLORS.BULLET
    )
  }

  update() {
    this.y -= BULLET_SPEED
    return this.y + this.height < 0
  }
}

export class Explosion extends Entity {
  private lifetime: number = 20
  private currentFrame: number = 0

  constructor(x: number, y: number) {
    super(
      x,
      y,
      ENEMY_WIDTH,
      ENEMY_HEIGHT,
      COLORS.EXPLOSION
    )
  }

  update(): boolean {
    this.currentFrame++
    return this.currentFrame >= this.lifetime
  }

  draw(ctx: CanvasRenderingContext2D) {
    const alpha = 1 - this.currentFrame / this.lifetime
    ctx.fillStyle = this.color
    ctx.globalAlpha = alpha
    ctx.fillRect(this.x, this.y, this.width, this.height)
    ctx.globalAlpha = 1
  }
}
